.wcb-container__wrap {
	position: relative;
}

.wcb-container__inner {
	position: initial;
	width: 100%;
	display: flex;
	flex: 1 1 0%;
	margin: 0 auto;
	> * {
		width: 100%;
	}
	.wcb-container__wrap {
		&.is_wcb_container_child {
			max-width: unset;
			flex: 1 1 auto;
		}
	}
}
